@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Fraunces:ital,opsz,wght@0,9..144,700;1,9..144,700&family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap");
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  background-color: hsl(30, 38%, 92%);
}

.container {
  display: flex;
  width: 600px;
  flex-direction: row;
  margin: 10% auto;
  border-radius: 10px;
  overflow: hidden;
  background-color: white;
}

.img-produto {
  width: 300px;
  height: 450px;
  display: flex;
  background-image: url(/product-preview-card-component-main/images/image-product-desktop.jpg);
  background-size: cover;
  background-position: center;
}

.descrição {
  width: 300px;
  height: 450px;
  padding: 32px;
}

.perfume {
  margin-bottom: 20px;
  font-family: "Montserrat", sans-serif;
  font-size: 12px;
  font-weight: 500;
  letter-spacing: 5px;
  line-height: 14.63px;
}

.título {
  width: 236px;
  margin-bottom: 24px;
  font-family: "Fraunces", serif;
  font-style: normal;
  font-weight: 700;
  font-size: 32px;
  line-height: 32px;
  color: black;
}

.parágrafo {
  margin-bottom: 29px;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 23px;
}

.preço {
  display: flex;
  gap: 19px;
  align-items: center;
  margin-bottom: 30px;
}

h2 {
  font-size: 32px;
  line-height: 32px;
  font-family: "Fraunces";
  font-style: normal;
  font-weight: 700;
  font-size: 32px;
  line-height: 32px;
  color: #3D8168;
}

p {
  font-size: 13px;
  line-height: 23px;
  text-decoration: line-through;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  color: #6C7289;
}

.botão {
  width: 236px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  background-color: #3D8168;
  font-size: 14px;
  border: none;
  color: white;
  cursor: pointer;
  font-family: "Montserrat";
  font-weight: 500;
  line-height: 17px;
}

.img-carrinho {
  margin-right: 11.61px;
}

button:hover {
  background-color: #1A4032;
}